.error{ height: 100%;

    .error-inner {  text-align: center; }
    .title { margin-top: 15px; font-size: 32px; color:$colorB; font-weight: 700; }
    .desc  { margin: 15px auto 0; font-size: 16px; 
        max-width: 380px;
    }

}



/* ===================== 로딩 loading =====================*/
.page_loading { display: none;
    position: fixed;top: 0;left: 0;
    width: 100%;  height: 100%; min-height: 100%; z-index: 999999; 
	overflow-x: hidden;overflow-y: hidden;
    background-color: rgba(0,0,0, 0.5);
    .center_teable { display: table; table-layout: fixed; width: 100%;  height: 100%;}
    .center_center { display: table-cell;width: 100%;
        padding: 20px 0;text-align: center;vertical-align: middle;   
        .img{ display: inline-block; background-color: #fff;};
    }
}
/* //===================== 로딩 loading =====================*/
