// how to use : Use following mixins.
// variable : [X-axis] [Y-axis] [offset_x] [offset_y] [width] [height] [total_width] [total_height] [image_url] [image_name] [folder_name]
    $checkbox-dim: 86px 40px -86px -40px 20px 20px 146px 141px '../images/common/sp_form.png' 'checkbox-dim' 'sp_form';
    $checkbox-off: 40px 86px -40px -86px 20px 20px 146px 141px '../images/common/sp_form.png' 'checkbox-off' 'sp_form';
    $checkbox-on: 80px 86px -80px -86px 20px 20px 146px 141px '../images/common/sp_form.png' 'checkbox-on' 'sp_form';
    $dot_tooltip-top: 33px 126px -33px -126px 13px 15px 146px 141px '../images/common/sp_form.png' 'dot_tooltip-top' 'sp_form';
    $dot_tooltip: 0px 126px 0px -126px 13px 15px 146px 141px '../images/common/sp_form.png' 'dot_tooltip' 'sp_form';
    $icon-calender: 43px 0px -43px 0px 23px 23px 146px 141px '../images/common/sp_form.png' 'icon-calender' 'sp_form';
    $icon-input-x: 0px 86px 0px -86px 20px 20px 146px 141px '../images/common/sp_form.png' 'icon-input-x' 'sp_form';
    $icon-time: 0px 0px 0px 0px 23px 23px 146px 141px '../images/common/sp_form.png' 'icon-time' 'sp_form';
    $icon_calender: 0px 43px 0px -43px 23px 23px 146px 141px '../images/common/sp_form.png' 'icon_calender' 'sp_form';
    $icon_input-x: 126px 0px -126px 0px 20px 20px 146px 141px '../images/common/sp_form.png' 'icon_input-x' 'sp_form';
    $icon_tooltip: 43px 43px -43px -43px 21px 21px 146px 141px '../images/common/sp_form.png' 'icon_tooltip' 'sp_form';
    $radio-dim: 86px 0px -86px 0px 20px 20px 146px 141px '../images/common/sp_form.png' 'radio-dim' 'sp_form';
    $radio-off: 126px 40px -126px -40px 20px 20px 146px 141px '../images/common/sp_form.png' 'radio-off' 'sp_form';
    $radio-on: 126px 80px -126px -80px 20px 20px 146px 141px '../images/common/sp_form.png' 'radio-on' 'sp_form';

//ancestor
    $sp_form-width: 146px;
    $sp_form-height: 141px;
    $sp_form-image: '../images/common/sp_form.png';
    $sp_form-sprites: ($checkbox-dim, $checkbox-off, $checkbox-on, $dot_tooltip-top, $dot_tooltip, $icon-calender, $icon-input-x, $icon-time, $icon_calender, $icon_input-x, $icon_tooltip, $radio-dim, $radio-off, $radio-on, );
    $sp_form_sprites_name: (checkbox-dim, checkbox-off, checkbox-on, dot_tooltip-top, dot_tooltip, icon-calender, icon-input-x, icon-time, icon_calender, icon_input-x, icon_tooltip, radio-dim, radio-off, radio-on, );
    $sp_form: (146px, 141px, '../images/common/sp_form.png', $sp_form-sprites, );

//mixin

        %sp_form {
        background-image: url('../images/common/sp_form.png');
        }

        @mixin sp_form($sprite) {
        @extend %sp_form;
        @include spriteSizeSupport1x($sprite);
        @include spriteBackgroundPositionSupport1x($sprite);
        }
