@charset "utf-8";

/* _variables.scss */

//root path
//$G-path: '../';
$G-width:      1200px;  // 사이트 컨텐츠 크기
$G-maxWidth:   1920px;  // 디자안 최대크기
$G-wMobile    : 768px;  // 미디어쿼리 모바일
$G-minWidth   : 320px;  // 최소 모바일 



//img path
$Gpath-img:  '../images/';

//body color
//$G-body-color: #2f2d2d;

// Color 
$colorMain: #68b435;
$colorSub : #ee7a56;
$colorBGp : #00a99d;
$colorBGd : #f8f8f8;
$colorBGw : #e0e0e0;


// fonts Name
$baseFT  : "Nanum Gothic";     // 나눔 고딕 (기본컬러)
$subFT   : "Nanum Square";     // 나눔 스쿼어 (title, 영문, 숫자)
$colorB  : #222222;  // 강조 컬러 
$colorD  : #454545;  // 기본 컬러 
$colorS  : #777777;  // 서브 컬러
$colorL  : #999999;  // 저체도 컬러, input:placeholder
$colorP  : #00a99d;  // 포인트 컬러 ( hover 009999)
$colorE  : #f24848;  // 에러 경고 


// form Color
$formH   : 40px    ;       // text, select 높이
$lineC_d : #cdcdcd ;     // 라인 기본값
$lineC_s : #3e73d1  ;    // 라인 성공
$lineC_e : #dd4c5c ;     // 라인 경고 에러
$lineC_f : #3e73d1  ;    // 라인 포커스 
    

// border
$borderTop : 2px solid #555555;
$borderLine : 1px solid #d8d8d8;
$borderBottom : 1px solid #d8d8d8;