// 기본 컬러
.ft_t {  font-family: $baseFT;  font-weight: 100; }   // Thin
.ft_l {  font-family: $baseFT;  font-weight: 300; }   // Light
.ft_r {  font-family: $baseFT;  font-weight: 400; }   // Regular
.ft_m {  font-family: $baseFT;  font-weight: 500; }   // Medium
.ft_b {  font-family: $baseFT;  font-weight: 700; }   // Bold
.ft_bb { font-family: $baseFT;  font-weight: 900; }   // Black// 기본 컬러

.ft_st {  font-family: $subFT;  font-weight: 100; }   // Thin
.ft_sl {  font-family: $subFT;  font-weight: 300; }   // Light
.ft_sr {  font-family: $subFT;  font-weight: 400; }   // Regular
.ft_sm {  font-family: $subFT;  font-weight: 500; }   // Medium
.ft_sb {  font-family: $subFT;  font-weight: 700; }   // Bold
.ft_sbb { font-family: $subFT;  font-weight: 900; }   // Black

