// slider  
$sliderBG_d  : #ffffff;    // 슬라이터 bg 기본 
$sliderBG_a  : #5ba429;     // 슬라이터 bg 활성화 
$sliderDra_d : #ffffff;    // 슬라이터 드레거 기본 
$sliderDra_a : #686868;     // 슬라이터 드레거 활성화 

// 슬라이터 BG
.ui-widget-content { background-color: $sliderBG_d; }
.ui-widget-header { background-color: $sliderBG_a;  }

// 슬라이더 드래거
.ui-slider-handle.ui-corner-all.ui-state-default { background-color: $sliderDra_d; }
.ui-slider-handle.ui-corner-all.ui-state-default.ui-state-active { background-color: $sliderDra_a; }