/* footer.scss */
#footer{ 


    .scroll_top {  display: none;
        position: absolute; bottom: 50px; left: 50%;  width: 55px; height: 55px; 
        margin: 0 0 0 540px; 
        cursor: pointer; z-index: 100;
        a { position: relative; display: block; width: 55px; height: 55px; 
            background-color: #fff; border-radius: 50%; 
            font-size: 12px; font-weight: bold; color:#000;
            border:1px solid #efefef;
            box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.05);
            &:after { content: "＾"; display: inline-block; position: absolute; top:10px; left: 23px;
                /* @include sp_icons($icon_scroll_top); */ }
            span { display: block; position: absolute; top:22px; width: 100%; text-align: center; }
        }

    }

}
