@charset "utf-8";

/* ====== popup ====== */    
.icon-pop_close { display: inline-block; width: 28px; height: 28px; 
	&:after { content: ""; display: block;  @include sp_icons($icon-pop_close);  }
}   


/* ====== PAGING ====== */    
.icon-page_prev { display: inline-block; width: 20px; height: 20px;
	&:after { content: ""; display: block;  @include sp_icons($icon-page_prev);  }
}   
.icon-page_prevprev { display: inline-block; width: 20px; height: 20px;
	&:after { content: ""; display: block; @include sp_icons($icon-page_prevprev); }
}   
.icon-page_prev { display: inline-block; width: 20px; height: 20px;
	&:after { content: ""; display: block; @include sp_icons($icon-page_next); }
}   
.icon-page_prev { display: inline-block; width: 20px; height: 20px;
	&:after { content: ""; display: block; @include sp_icons($icon-page_nextnext); }
}   

