// how to use : Use following mixins.
// variable : [X-axis] [Y-axis] [offset_x] [offset_y] [width] [height] [total_width] [total_height] [image_url] [image_name] [folder_name]
    $accordion_arrow_down: 0px 0px 0px 0px 30px 30px 160px 118px '../images/common/sp_icons.png' 'accordion_arrow_down' 'sp_icons';
    $accordion_arrow_up: 50px 0px -50px 0px 30px 30px 160px 118px '../images/common/sp_icons.png' 'accordion_arrow_up' 'sp_icons';
    $icon-page_next-on: 140px 40px -140px -40px 20px 20px 160px 118px '../images/common/sp_icons.png' 'icon-page_next-on' 'sp_icons';
    $icon-page_next: 0px 98px 0px -98px 20px 20px 160px 118px '../images/common/sp_icons.png' 'icon-page_next' 'sp_icons';
    $icon-page_nextnext-on: 100px 0px -100px 0px 20px 20px 160px 118px '../images/common/sp_icons.png' 'icon-page_nextnext-on' 'sp_icons';
    $icon-page_nextnext: 100px 40px -100px -40px 20px 20px 160px 118px '../images/common/sp_icons.png' 'icon-page_nextnext' 'sp_icons';
    $icon-page_prev-on: 140px 80px -140px -80px 20px 20px 160px 118px '../images/common/sp_icons.png' 'icon-page_prev-on' 'sp_icons';
    $icon-page_prev: 40px 98px -40px -98px 20px 20px 160px 118px '../images/common/sp_icons.png' 'icon-page_prev' 'sp_icons';
    $icon-page_prevprev-on: 80px 98px -80px -98px 20px 20px 160px 118px '../images/common/sp_icons.png' 'icon-page_prevprev-on' 'sp_icons';
    $icon-page_prevprev: 140px 0px -140px 0px 20px 20px 160px 118px '../images/common/sp_icons.png' 'icon-page_prevprev' 'sp_icons';
    $icon-pop_close: 0px 50px 0px -50px 28px 28px 160px 118px '../images/common/sp_icons.png' 'icon-pop_close' 'sp_icons';
    $pop_close: 48px 50px -48px -50px 28px 28px 160px 118px '../images/common/sp_icons.png' 'pop_close' 'sp_icons';

//ancestor
    $sp_icons-width: 160px;
    $sp_icons-height: 118px;
    $sp_icons-image: '../images/common/sp_icons.png';
    $sp_icons-sprites: ($accordion_arrow_down, $accordion_arrow_up, $icon-page_next-on, $icon-page_next, $icon-page_nextnext-on, $icon-page_nextnext, $icon-page_prev-on, $icon-page_prev, $icon-page_prevprev-on, $icon-page_prevprev, $icon-pop_close, $pop_close, );
    $sp_icons_sprites_name: (accordion_arrow_down, accordion_arrow_up, icon-page_next-on, icon-page_next, icon-page_nextnext-on, icon-page_nextnext, icon-page_prev-on, icon-page_prev, icon-page_prevprev-on, icon-page_prevprev, icon-pop_close, pop_close, );
    $sp_icons: (160px, 118px, '../images/common/sp_icons.png', $sp_icons-sprites, );

//mixin

        %sp_icons {
        background-image: url('../images/common/sp_icons.png');
        }

        @mixin sp_icons($sprite) {
        @extend %sp_icons;
        @include spriteSizeSupport1x($sprite);
        @include spriteBackgroundPositionSupport1x($sprite);
        }
