/* ui_common.scss */

/* ====== 버튼 ====== */
.btn {  position: relative; box-sizing: border-box; 
    display: inline-block; padding:8px 10px 8px;
    margin-bottom: 0;
    font-weight: bold;line-height: 1.42857143;text-align: center;
    letter-spacing: -1px; white-space: nowrap; vertical-align: middle;
    -ms-touch-action: manipulation;touch-action: manipulation;
    cursor: pointer;
    -webkit-user-select: none;-moz-user-select: none;-ms-user-select: none;
    user-select: none; background-image: none;
    border: 1px solid transparent;
    -webkit-appearance: none;
    -moz-appearance: none;
}

// 비활성화
.btn:disabled
{ @include opacity(0.5); pointer-events:none; }


// 색깔
.btn.btn_default { font-size: 16px; min-width: 160px;
    background:#fff; border:1px solid #555555; color:$colorB;
}
.btn.btn_default:hover {  background:#f8f8f8;  border-color: #111111;}

.btn.btn_gray { font-size: 16px; min-width: 160px;
    background:#777777; border:1px solid #777777; color:#fff;}
.btn.btn_gray:hover {  background:#444444; border-color:#444444;}

.btn.btn_black { font-size: 16px; min-width: 160px;
    background:#444444; border:1px solid #444444; color:#fff;}
.btn.btn_black:hover {  background:#111111;  border-color: #111111;}


// 크기
.btn.btn_lg { padding:11px 20px 12px;  min-width: 200px !important; font-size: 18px; }
.btn.btn_sm { padding: 6px 10px 8px;   min-width: auto;  font-size: 14px; }


// 버튼 정열
.btn_bottom { margin-top: 40px;}
.btn_set { @include clearfix(); 
    .btng-c   { text-align: center; }
    .btng-l  {  float: left; }
    .btng-r  { float: right;}
}
.btn_set-center { text-align: center; }
.btn_set-single  { text-align: center;  padding: 0px;
    .btn_item { a , button, input[type="button"] { width: 100%;} }
}
.btn_set-double { padding: 0px;
    &::after { content: ""; display: block; clear: both;}
    .btn_item { position: relative; width: 50%; float: left; a { width: 100%;} }
    .btn_item:first-child { padding-right:2px; }
    .btn_item:last-child  { padding-left:2px;  }/*  */
}
.btn_set-triple { padding: 0px;
    &::after { content: ""; display: block; clear: both;}
    .btn_item { position: relative; width: 33.3333%; padding: 0 2px; float: left; a { width: 100%;} }
    .btn_item:first-child { padding-left:0px; }
    .btn_item:last-child  { padding-right:0px; }
}



/* ====== PAGING ====== */    
.paging-area { width:100%; clear:both;position:relative; margin:20px 0 10px; letter-spacing:0px; text-align:center;
    .paging { position:relative; text-align:center; }
    .paging a,
    .paging strong { position:relative; display:inline-block; color:#666;
        min-width: 36px; height:36px; line-height:36px;
        text-decoration:none; vertical-align:top; padding:0 10px;
        font-family: $subFT; font-size: 18px;
        border:1px solid #dcdcdc; box-sizing: border-box;
        em { font-style: normal; }
        &:after {  }
    }
    .paging strong.on { background:#444444; color:#fff; border:1px solid #444444; }
    .paging a:hover { background:#ddd;}
    .paging a:focus { background:#ddd;}

    .paging a.prev, .paging a.prev_end, .paging a.next, .paging a.next_end {
        border:1px solid #dcdcdc; box-sizing: border-box;
        &:after { content:""; display: block; position: absolute; top:7px; left:8px; }
     }
    .paging a.prev { width: 36px;  padding:0px;  text-indent: -9999px;
        &:after { @include sp_icons($icon-page_prev);     } 
    }
    .paging a.prev_end { width: 36px;  padding:0px;  margin-right:5px; text-indent: -9999px;
        &:after { @include sp_icons($icon-page_prevprev); }
    }
    .paging a.prev:hover     {  /* &:after { @include sp_icons($icon-page_prev-on) };     */ }
    .paging a.prev_end:hover {  /* &:after { @include sp_icons($icon-page_prevprev-on) }; */ }

    .paging a.next { width: 36px;  padding:0px;  margin-left:5px; text-indent: -9999px;
        &:after { @include sp_icons($icon-page_next);     }
    }
    .paging a.next_end { width: 36px;  padding:0px;  margin-left:5px; text-indent: -9999px;
        &:after { @include sp_icons($icon-page_nextnext); }
    }
    .paging a.next:hover     {  /* &:after { @include sp_icons($icon-page_prev-on) };     */ }
    .paging a.next_end:hover {  /* &:after { @include sp_icons($icon-page_prevprev-on) }; */ }

   
}




/* ================== TAB MENU */
.tab-wrap { position: relative; }
.tab-list { display: inline-block; position:relative;
    margin:0; padding:0; width:100%; 
    >li { display:inline; width:100%; list-style:none; box-sizing: border-box; 
        .btn-area{ position:absolute; height:50px; display:block;
            text-decoration:none; text-align: center; line-height:2; color:#000;
            a { font-size: 20px; font-weight: bold;background-color: #dadada;
                .tab_name {}
                @include wordBreak-all();    
            }
        }	
        .st_table{ display: table; width: 100%; height: 100%;
            a { display: table-cell; vertical-align: middle; }
        }
    }	
    >li >.con-area{ display: none; 
        width:100%; margin-top:50px; margin-left:0px; left:0px; 
        border: 0px solid #ccc; padding:0 0px}	
    >.on .btn-area a { color:#ffffff; background-color: $colorP;}
    >.on > .con-area { display: block;}
    
    /* ================== TAB MENU tab_type1 */
    &.tab_type1 { 
        >li {  background: #eaeaea;
            .btn-area{ color:$colorD; 
                border: 1px solid #d0d0d0; border-bottom:2px solid $colorP; border-left:none;
                a { background-color: #eaeaea; }
            }	
            .st_table{
                a { }
            }
            &:first-child  .btn-area{ border-left:1px solid #d0d0d0;}
        }	
        >li >.con-area{ }	
        >.on .btn-area {
            border: 2px solid $colorP !important; border-bottom: none !important;
            a { color:$colorB; font-weight: bold;background-color: #fff;}
        }
        >.on > .con-area { }
        >.on_prev { 
            .btn-area{border-right: 0px solid transparent;}
        }
    }

    /* ================== TAB MENU setting */
    &.tab_col2 { 
        >li .btn-area { top:0; left:0;   width:50%; }
        >li:nth-child(1) .btn-area{ left:0;  }
        >li:nth-child(2) .btn-area{ left:50%;}
    }
    &.tab_col3 { 
        >li .btn-area { top:0; left:0; width:33.333333333%; border-left:1px solid #cccccc;}
        >li:nth-child(1) .btn-area{ left:0; border-left:none;}
        >li:nth-child(2) .btn-area{ left:33.3333333333%; } 
        >li:nth-child(3) .btn-area{ left:66.6666666666%; }
    }
    &.tab_col4 { 
        >li .btn-area { top:0; left:0;   width:25%; }
        >li:nth-child(1) .btn-area{ left:0;       }
        >li:nth-child(2) .btn-area{ left:25%; }
        >li:nth-child(3) .btn-area{ left:50%; }
        >li:nth-child(4) .btn-area{ left:75%; }
    }
    &.tab_col5 { 
        >li .btn-area { top:0; left:0;   width:20%; }
        >li:nth-child(1) .btn-area{ left:0;       }
        >li:nth-child(2) .btn-area{ left:20%; }
        >li:nth-child(3) .btn-area{ left:40%; }
        >li:nth-child(4) .btn-area{ left:60%; }
        >li:nth-child(5) .btn-area{ left:80%; }
    }
}



/* ================== accordion MENU */
.accordion-wrap.noneline { border-top: 0 }
.accordion-wrap { border-top: $borderTop; }
// 일반 아코디언
.accordion-list{
    >li { border-bottom: 1px solid #e1e1e1; 
        .header-area { position: relative; 
            background-color: $colorBGd;
            .accordion_name  { display: block; 
                @include ellipsis();
            }
            .acc_btn {  position: relative; box-sizing: border-box;
                 display: block; padding: 20px 50px 20px 20px;
                &:after { content: ""; display: block; 
                    position: absolute; top:18px; right:15px;
                    @include sp_icons($accordion_arrow_down);}
            }
        }
        >.con-area { display: none;  padding: 30px 20px;  }
    }
    >li.on {
        .tit-area {
            .acc_btn { &:after { @include sp_icons($accordion_arrow_up);} }
        }
    }
}




